<template>
  <div class="seller">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="star-wrapper">
          <star :size="36" :score="seller.score" class="star"></star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h3>起送价</h3>
            <span class="stress">{{seller.minPrice}}</span>元
          </li>
          <li class="block">
            <h3>商家配送</h3>
            <span class="stress">{{seller.deliveryPrice}}</span>元
          </li>
          <li class="block">
            <h3>平均配送时间</h3>
            <span class="stress">{{seller.deliveryTime}}</span>分钟
          </li>
        </ul>
        <div class="favorite" @click="toggleFavorite">
          <i class="icon-favorite" :class="{'active': favorite}"></i>
          <div class="text">{{favoriteText}}</div>
        </div>
      </div>
      <split></split>
      <div class="bulletin">
        <h3 class="title">公告与活动</h3>
        <div class="text">{{seller.bulletin}}</div>
      </div>
      <div v-if="seller.supports" class="supports">
        <ul>
          <li v-for="(support,index) in seller.supports" :key="index" class="support-item">
            <span class="icon" :class="classMap[seller.supports[index].type]"></span>
            <span class="text">{{support.description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h3 class="title">商家实景</h3>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="picList">
            <li class="pic-item" v-for="(pic,index) in seller.pics" :key="index">
              <img :src="pic" alt="" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h3 class="title">商家信息</h3>
        <ul>
          <li class="info-item" v-for="(info,index) in seller.infos" :key="index">
            {{info}}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import star from '@/components/star'
import split from '@/components/split'
import BScroll from 'better-scroll'
export default {
  components: {
    star,
    split
  },
  data () {
    return {
      favorite: true
    }
  },
  computed: {
    favoriteText () {
      console.log(this.favorite)
      return this.favorite ? '已收藏' : '收藏'
    }
  },
  props: {
    seller: Object
  },
  methods: {
    toggleFavorite () {
      this.favorite = !this.favorite
    }
  },
  updated () {
    if (!this.picScroll) {
      if (this.seller.pics) {
        this.$nextTick(() => {
          let picWidth = 120
          let margin = 6
          let width = (picWidth + margin) * this.seller.pics.length - margin
          this.$refs.picList.style.width = width + 'px'
          this.picScroll = new BScroll(this.$refs.picWrapper, {
            scrollX: true,
            eventPassthrough: 'vertical'
          })
        })
      }
    } else {
      this.picScroll.refresh()
    }
  },
  created () {
    if (!this.scroll) {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$el, {click: true})
      })
    } else {
      this.scroll.refresh()
    }
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
  }
}
</script>

<style lang="stylus">
   @import "../common/css/mixin.styl"
  .seller
    position: absolute
    top: 174px
    left: 0
    bottom: 48px
    width: 100%
    overflow: hidden
    .overview
      position: relative
      padding: 18px
      .title
        margin: 0 0 8px 0
        line-height: 14px
        font-size: 14px
        color: rgb(7,17,27)
      .star-wrapper
        padding-bottom: 18px
        border-1px(rgba(7,17,27,0.1))
        .star,.text
          display:inline-block
          line-height: 18px
          font-size: 10px
          color:rgb(77,85,96)
        .star
          margin-right: 8px
        .text
          margin-right: 12px
      .remark
        display: flex
        margin-top: 18px
        .block
          flex: 1
          border-right:1px solid rgba(7,17,27,0.1)
          text-align: center
          &.block:last-child
            border-right:none
          h3
            font-size: 10px
            color: rgb(147,153,159)
            line-height: 10px
            margin: 0 0 4px 0
          .stress
            line-height: 24px
            font-size: 24px
            font-weight: 200
            color: rgb(7,17,27)
      .favorite
        position: absolute
        right 11px
        top: 18px
        width: 50px
        text-align:center
        font-size: 0
        .icon-favorite
          margin-bottom: 4px
          line-height: 24px
          font-size: 24px
          color:rgb(147,153,159)
          &.active
            color:rgb(240,20,20)
        .text
          line-height: 10px
          font-size: 10px
          color:rgb(77,85,93)
    .bulletin
      padding: 18px 18px 0
      .title
        line-height: 14px
        font-size: 14px
        color: rgb(7,17,27)
      .text
        padding: 0 12px 16px
        line-height: 24px
        font-size: 12px
        font-weight: 200
        color:rgb(240,20,20)
        border-1px(rgba(7,17,27,0.1))
    .supports
      padding:0 18px
      margin:0 auto
      .support-item
        padding: 16px 12px
        font-size: 0
        border-1px(rgba(7,17,27,0.1))
        &:last-child
          border-none()
        .icon
          display: inline-block
          width: 16px
          height: 16px
          vertical-align: top
          margin-right: 6px
          background-size: 16px 16px
          background-repeat: no-repeat
          &.decrease
            bg-image('decrease_3')
          &.discount
            bg-image('discount_3')
          &.special
            bg-image('special_3')
          &.invoice
            bg-image('invoice_3')
          &.guarantee
            bg-image('guarantee_3')
        .text
          line-height: 16px
          font-size: 12px
          font-weight: 200
          color: rgb(7,17,27)
    .pics
      padding: 18px
      .title
        margin-bottom: 12px
        line-height: 14px
        color: rgb(7,17,27)
        font-size: 14px
      .pic-wrapper
        width: 100%
        overflow: hidden
        white-space: nowrap
        .pic-list
          font-size: 0
          .pic-item
            display: inline-block
            margin-right: 6px
            width: 120px
            height: 90px
            &:last-child
              margin-right:none
    .info
      padding: 18px 18px 0 18px
      .title
        padding-bottom: 18px
        margin: 0
        border-1px(rgba(7,17,27,0.1))
      .info-item
        padding: 16px 12px
        line-height: 16px
        font-size: 12px
        font-weight: 200
        color: rgb(7,17,27)
        border-1px(rgba(7,17,27,0.1))
</style>



