<template>
  <div id="app">
    <v-header :seller = seller></v-header>
    <div class="tab border-1px">
      <div class="tab-item"><router-link to="/goods">商品</router-link></div>
      <div class="tab-item"><router-link to="/ratings">评价</router-link></div>
      <div class="tab-item"><router-link to="/seller">商家</router-link></div>
    </div>
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import vHeader from '@/components/header'
import data from '@/util/mock.js'
export default {
  name: 'App',
  data () {
    return {
      seller: {}
    }
  },
  components: {
    vHeader
  },
  methods: {
    getData (data) {
      this.$http({
        url: 'http://58js.win',
        method: 'GET'
      }).then((res) => {
      // console.log('获取mock数据:')
      // console.log(res.data.seller[0])
        this.seller = res.data.seller[0]
      })
    }
  },
  created () {
    this.getData(data)
  }
}
</script>

<style  lang="stylus">
@import 'common/css/mixin.styl'
// @import "./common/style.css"
.tab
  display: flex
  width: 100%
  height: 40px
  line-height: 40px
  border-bottom:1px solid rgba(7,17,27,0.1)
  .tab-item
    flex: 1
    text-align:center
    & > a
      display: block
      font-size: 14px
      color: rgb(77,85,93)
      &.router-link-active{
        color:red
      }
</style>



