<template>
  <div class="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周围商家{{seller.rankRate}}%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.serviceScore" class="star"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.foodScore" class="star"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="time">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect @increment="incrementTotal" :select-type="selectType" :only-content="onlyContent" :ratings="ratings"></ratingselect>
      <div class="rating-wrapper" ref="ratingWrapper">
        <ul>
          <li v-show="needShow(rating.rateType,rating.text)" v-for="(rating,index) in ratings" :key="index" class="rating-item">
            <div class="avatar">
              <img :src="rating.avatar" alt="" width="28" height="28">
            </div>
            <div class="content">
              <h3 class="name">{{rating.username}}</h3>
              <div class="star-wrapper">
                <star :size="24" :score="rating.score" class="star"></star>
                <span class="delivery" v-show="rating.deliveryTime && !rating.deliveryTime.length">{{rating.deliveryTime}}分钟送达</span>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                <span :class="{'icon-thumb_up':rating.rateType === 0, 'icon-thumb_down': rating.rateType === 1}"></span>
                <span v-for="(item,index) in rating.recommend" :key="index" class="recommend-item">{{item}}</span>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import star from '@/components/star'
import split from '@/components/split'
import ratingselect from '@/components/ratingselect'
import BScroll from 'better-scroll'
const ALL = 2
export default {
  components: {
    star,
    split,
    ratingselect
  },
  data () {
    return {
      ratings: [],
      selectType: ALL,
      onlyContent: true
    }
  },
  props: {
    seller: {
      type: Object
    }
  },
  methods: {
    getData (data) {
      this.$http({
        url: 'http://58js.win',
        method: 'GET'
      }).then((res) => {
        // console.log('获取mock数据-ratings')
        // console.log(res.data.ratings)
        this.ratings = res.data.ratings
        this.$nextTick(() => {
          this._initScroll()
        })
      })
    },
    // 接收从子组件ratingselect传回来的状态，并刷新滚动高度
    incrementTotal (type, data) {
      this[type] = data
      // this.$nextTick(() => {
      //   this.scroll.refresh()
      // })
    },
    _initScroll () {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$el, {
          click: true
        })
      } else {
        this.scroll.refresh()
      }
    },
    // 过滤，判断评价的内容
    needShow (type, text) {
      if (this.onlyContent && !text) {
        return false
      }
      if (this.selectType === ALL) {
        return true
      } else {
        return type === this.selectType
      }
    }
  },
  created () {
    this.getData()
  }
}
</script>

<style lang="stylus">
  @import "../common/css/mixin.styl"
  .ratings
    position: absolute
    top: 174px
    bottom: 48px
    left: 0
    width: 100%
    overflow: hidden
    .overview
      display: flex
      padding: 18px 0
      .overview-left
        flex: 0 0 137px
        width: 137px
        padding-bottom: 6px
        border-right: 1px solid rgba(7,17,27,0.1)
        text-align:center
        @media only screen and (max-width 320px)
          flex 0 0 110px
          width 110px
        .score
          margin-bottom: 6px
          line-height: 28px
          font-size: 24px
          color: rgb(255,153,0)
        .title
          margin-bottom: 8px
          line-height: 12px
          font-size: 12px
          color: rgb(7,17,27)
        .rank
          line-height: 10px
          font-size: 10px
          color: rgb(147,153,159)
      .overview-right
        flex: 1
        padding: 6px 0 6px 24px
        @media only screen and (max-width 320px)
          padding-left: 6px
        .score-wrapper
          font-size: 0
          .title,.star,.score
            display: inline-block
          .title
            line-height: 18px
            font-size: 12px
            color: rgb(7,17,27)
            margin: 0 12px 8px 0
            vertical-align: top
          .star
            margin-right: 6px
          .score
            font-size: 12px
            line-height: 18px
            vertical-align: top
            color: rgb(255,153,0)
        .delivery-wrapper
          font-size: 0
          .title,.time
            line-height: 18px
            font-size: 12px
          .title
            color: rgb(7,17,27)
            margin-right: 12px
          .time
            color: rgb(147,153,159)
    .rating-wrapper
      .rating-item
        display: flex
        margin: 18px
        padding-bottom: 12px
        border-1px(rgba(7,17,27,0.1))
        .avatar
          flex: 0 0 28px
          width: 28px
          margin-right: 12px
          img
            border-radius: 50%
        .content
          flex: 1
          .name
            margin: 0 0 4px 0
            line-height: 12px
            font-size: 10px
            color: rgb(7,17,27)
        .star-wrapper
          font-size: 0
          .star,.delivery
            display: inline-block
          .delivery
            line-height: 12px
            font-size: 10px
            font-weight: 200
            color: rgb(147,153,159)
            margin-left: 6px
        .text
          margin-top: 6px
          margin-bottom: 8px
          font-size: 10px
          font-weight: 200
          color: rgb(7,17,27)
          line-height: 18px
        .recommend
          .icon-thumb_up
            color: rgb(0,160,220)
            font-size: 12px
            line-height: 16px
          .recommend-item
            margin-right: 6px
            padding: 2px 6px
            margin-bottom: 6px
            display: inline-block
            line-height:16px
            border:1px solid (rgba(7,17,27,0.1))
            border-radius: 2px
            font-size: 9px
            color: rgb(147,153,159)
</style>



