<template>
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li v-for="(item,index) in goods" :key="index" class="menu-item" :class="{'current': currentIndex === index}" @click="selectMenu(index)">
          <span class="text border-1px">
           <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span> {{ item.name }}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="(item,index) in goods" :key="index" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="(food,index) in item.foods" @click="selectFood(food,$event)" class="food-item border-1px" :key="index">
              <div class="icon">
                <img :src="food.icon" alt="">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food = "food" @increment = "incrementTotal"></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart :select-foods ="selectFoods" :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice" ref="shopCart"></shopcart>
    <food :food = "selectGoods" ref="food"></food>
  </div>
</template>

<script>
import data from '@/util/mock.js'
import BScroll from 'better-scroll'
import shopcart from '@/components/shopcart'
import cartcontrol from '@/components/cartcontrol'
import food from '@/components/food'
export default {
  name: 'goods',
  props: {
    seller: {
      type: Object
    }
  },
  components: {
    shopcart,
    cartcontrol,
    food
  },
  data () {
    return {
      goods: [],
      listHeight: [],
      scrollY: 0,
      selectGoods: {}
    }
  },
  computed: {
    // 计算当前的滚动距离属于哪个listHeight，返回listHeight的序列号
    currentIndex () {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i]
        let height2 = this.listHeight[i + 1]
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i
        }
      }
      return 0
    },
    // 遍历选中的商品，把选中的push到数组foods
    selectFoods () {
      let foods = []
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if (food.count) {
            foods.push(food)
          }
        })
      })
      // this.goods.forEach(function(good){

      // })
      return foods
    }
  },
  methods: {
    getData (data) {
      this.$http({
        url: 'http://58js.win',
        method: 'GET'
      }).then((res) => {
        // console.log('获取mock数据-goods:')
        this.goods = res.data.goods
        // this._initScroll()
        this.$nextTick(() => {
          this._initScroll()
          this._calculateHeight()
        })
      })
    },
    _initScroll () {
      // 运用better-scroll插件，获取滚动的dom进行滚动
      this.menuScroll = new BScroll(this.$refs.menuWrapper, {
        click: true
      })
      this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
        probeType: 3,
        click: true
      })
      this.foodsScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y))
      })
    },
    _calculateHeight () {
      this.$nextTick(() => {
        // 计算每个food-list-hook的高度
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
        let height = 0
        this.listHeight.push(height)
        for (let i = 0; i < foodList.length; i++) {
          let item = foodList[i]
          height += item.clientHeight
          this.listHeight.push(height)
        }
      })
      // console.log(this.listHeight) [0, 1546, 1724, 1902, 2384, 2714, 3044, 3526, 4312, 5250]获取每个food-list-hook的高度
    },
    selectMenu (index) {
      console.log(index)
      let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
      let el = foodList[index]
      this.foodsScroll.scrollToElement(el, 300)
    },
    incrementTotal (target) {
      this.$refs.shopCart.drop(target)
    },
    selectFood (food, event) {
      this.selectGoods = food
      this.$refs.food.show()
    }
  },
  // 初始化
  created () {
    this.getData(data)
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
  }
}
</script>

<style lang="stylus">
  @import "../common/css/mixin.styl"
  .goods
    display: flex
    position: absolute
    top: 175px
    bottom: 46px
    width: 100%
    overflow: hidden
    .menu-wrapper
      flex: 0 0 80px
      width: 80px
      background: #f3f5f7
      .menu-item
        display: table
        height: 54px
        width: 56px
        line-height: 14px
        padding:0 12px
        &.current
          position: relative
          z-index: 10
          margin-top: -1px
          background: #fff
          font-weight: 700
        .icon
          display: inline-block
          width: 12px
          height: 12px
          margin-right: 2px
          background-size:12px 12px
          vertical-align: middle
          &.decrease
            bg-image('decrease_3')
          &.discount
            bg-image('discount_3')
          &.special
            bg-image('special_3')
          &.invoice
            bg-image('invoice_3')
          &.guarantee
            bg-image('guarantee_3')
        .text
          display: table-cell
          width: 56px
          vertical-align: middle
          font-size: 12px
          border-1px(rgba(7,17,27,0.1))
    .foods-wrapper
      flex:1
      .title
        padding-left: 14px
        height: 26px
        line-height: 26px
        border-left:2px solid #d9dde1
        font-size: 12px
        color: rgb(147,153,159)
        background: #f3f5f7
      .food-item
        display: flex
        margin:18px
        padding-bottom: 18px
        border-1px(rgba(7,17,27,0.1))
        &:last-child
          border-none()
          margin-bottom: 0
        .icon
          flex: 0 0 57px
          margin-right: 10px
        .content
          flex: 1
          .name
            height: 14px
            margin: 2px 0 8px 0
            line-height: 14px
            color: rgb(7,17,27)
            font-size: 16px;
          .desc,.extra
            line-height: 10px
            font-size: 10px
            color: rgb(147,153,159)
          .desc
            margin-bottom: 8px
            line-height: 12px
          .extra
            .count
              margin-right: 12px
          .price
            font-weight: 700
            line-height: 24px
            .now
              margin-right: 8px
              font-size: 14px
              color: rgb(240,20,20)
            .old
              text-decoration: line-line-through
              font-size: 10px
              color: rgb(147,153,159)
          .cartcontrol-wrapper
              position: absolute
              right: 0
              bottom: 18px
</style>



